.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}
body, h1, h2, h3, h4, h5, h6, p, ol, ul { margin: 0; padding: 0;}
ol, ul { list-style: none;}
a { text-decoration: none;}
img {max-width: 100%; height: auto; display: block;}
html {font-size: 62.5%;}

.App {
  text-align: center;
  height: 100vh
}

.wrapper {
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
  font-size: 5rem;
  margin-top: 50px;
}

.flex {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin: 20px;
}

button {
  padding: 10px;
  border-radius: 5px;
  font-size: 1.5rem;
  background-color: #eae8d1;
}

button:hover {
  cursor: pointer;
  box-shadow: 5px 5px 5px 0 #fec558;
  background-color: #88bb40;
  color: #ffffff;
}

.displayCategory h2 {
  text-align: left;
  font-size: 2.5rem;
  margin-top: 30px;
}


.displayContainer {
  border: 2px solid#83aa46;
  display: flex;
  justify-content: space-between;
  margin: 20px;
  padding: 20px;
}

.content {
  display: flex;
  flex-direction: column;
}

.content h2 {
  padding-bottom: 20px;
  font-size: 2.5rem;
  margin-top: 0;
}

.content p {
  text-align: left;
  font-size: 1.8rem;
  line-height: 2.3rem;
}

.imgContainer{
  min-width: 40%;
  max-width: 50%;
  padding-left: 20px;
  display: flex;
  align-items: center;
}

.instructions {
  margin: 30px auto;
  font-size: 2rem;
}

footer {
  text-align: center;
  background-color: #aed9cf;
  padding: 20px 0;
  margin: 20px 0 0 0;
}

footer p {
  font-size: 1.5rem;
  color: #030303;
}

footer a {
  color: #030303;
}

footer a:hover {
  color: #fec558;
}


/*****************/
/* Media Queries */ 
/*****************/

@media (max-width: 940px) {
    .displayContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: 20px;
    padding: 20px;
  }

  .imgContainer {
    margin-top: 20px;
    padding-left: 0;
    min-width: 80%;
    max-width: 80%;
  }

  img {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (max-width: 545px) {

  .wrapper {
    width: 100%;
  }

  h1 {
    font-size: 4rem;
    margin-top: 30px;
  }

  .instructions {
    margin: 30px 20px;
    font-size: 1.8rem;
  }

  .flex h2 {
    font-size: 2rem;
    margin-top: 20px;
  }
  
  .content h2 {
    text-align: center;
  }

  .content p {
    font-size: 1.6rem;
  }

  .imgContainer {
    width: 100%;
  }
}
